* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Helvetica Neue", cursive;
  text-decoration: none;
  list-style: none;
  outline: none;
}

::selection {
  background-color: #34C759;
  color: #ffffff;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #1A1A1A;
  color: #ffffff;
}

body.darkBody {
  overflow: hidden;
}

body.darkBody::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.877);
  z-index: 1100;
}

body a {
  color: #ffffff;
}

body::-webkit-scrollbar {
  width: 11px;
}

body::-webkit-scrollbar-thumb {
  background-color: #34C759;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #13fa4d;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 20px;
}

.titles {
  position: relative;
  text-align: center;
  height: 140px;
}

.titles::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 2px;
  background-color: #34C759;
}

.titles .transparent_title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 100px;
  letter-spacing: 8px;
  color: #303030;
  cursor: default;
}

.titles .transparent_title::selection {
  background: none;
  color: #303030;
}

.titles .title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 30px;
  line-height: 60px;
  letter-spacing: 4px;
  color: #D9D9D9;
  width: 100%;
  z-index: 20;
}

.upIcon {
  position: fixed;
  top: 120%;
  right: 2%;
  border: 1px solid #4D4D4D;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s ease;
  animation: colorAnime infinite 0.6s alternate;
  z-index: 999;
}

@keyframes colorAnime {
  0% {
    border: 1px solid rgba(113, 255, 148, 0.7960784314);
    padding-bottom: 10px;
  }

  100% {
    border: 1px solid #4d4d4d;
    padding-bottom: 0;
  }
}

.upIcon.visibleUpBtn {
  top: 89%;
}

.upIcon:hover {
  border: 1px solid #34C759;
  padding-bottom: 15px;
  animation: none;
}

.upIcon:hover svg {
  fill: #34C759;
  animation: none;
}

.upIcon svg {
  transform: rotate(-90deg);
  fill: #4D4D4D;
  animation: iconAnime infinite 0.7s alternate;
}

@keyframes iconAnime {
  0% {
    fill: #4D4D4D;
  }

  100% {
    fill: rgba(113, 255, 148, 0.7960784314);
  }
}

header {
  background: linear-gradient(180deg, rgba(26, 26, 26, 0.0001) 0%, rgba(26, 26, 26, 0.2470588235)), linear-gradient(0deg, rgba(33, 33, 33, 0.144), rgba(33, 33, 33, 0.116)), url("../images/header_bg.jpg");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100%;
  padding-bottom: 164px;
}

header .header__section {
  position: fixed;
  top: 0;
  left: 50%;
