}

header .header__section .header__navbar .burger.close .bar3 {
  transform: rotate(405deg) translate(-7px, -7px);
}

header .header__section .header__navbar .navside {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 0;
  background: linear-gradient(#101a13, rgb(17, 24, 17), rgb(22, 22, 22));
  text-align: center;
  overflow: hidden;
  transition: 0.2s ease;
  z-index: 50;
}

header .header__section .header__navbar .navside.nav-active {
  height: 200px;
}

header .header__section .header__navbar .navside ul {
  margin-top: 30px;
}

header .header__section .header__navbar .navside ul li a {
  display: block;
  padding: 5px 0;
  font-size: 16px;
